import { useRef, useState, type FC } from 'react'

import type { Options } from '@popperjs/core'

import CLASS_NAME, { WRAPPER_CLASS } from './styles'
import Popover from '../'

const RIGHT_OPTIONS: Partial<Options> = {
  placement: 'right'
}

export const PopoverAnchorExample: FC = () => {
  const anchorEl = useRef<HTMLButtonElement | null>(null)
  const anchorElRight = useRef<HTMLButtonElement | null>(null)
  const [isOpen, setIsOpen] = useState(false)
  const [isOpenRight, setIsOpenRight] = useState(false)
  
  const onClickRight = (): void => {
    setIsOpenRight(prev => !prev)
  }
  
  const onCloseRight = (): void => {
    setIsOpenRight(false)
  }

  const onClick = (): void => {
    setIsOpen(prev => !prev)
  }

  const onClose = (): void => {
    setIsOpen(false)
  }

  // ========================================== //

  return (
    <div className={WRAPPER_CLASS}>
      <button ref={anchorEl} onClick={onClick}>
        Toggle popover bottom
      </button>
      <Popover
        isOpen={isOpen}
        onClose={onClose}
        anchorEl={anchorEl}
      >
        <div className={CLASS_NAME}>
          Popper Content
        </div>
      </Popover>

      <br/>

      <button ref={anchorElRight} onClick={onClickRight}>
        Toggle popover right
      </button>
      <Popover
        isOpen={isOpenRight}
        onClose={onCloseRight}
        anchorEl={anchorElRight}
        options={RIGHT_OPTIONS}
      >
        <div className={CLASS_NAME}>
          Popper Content
        </div>
      </Popover>
    </div>
  )
}


const OPTIONS: Partial<Options> = {
  placement: 'top-end',
  modifiers: [
    {
      name: 'offset',
      options: {
        offset: [-16, -50]
      }
    }
  ]
}


export const PopoverWithoutAnchor: FC = () => {
  const [isOpen, setIsOpen] = useState(false)
  
  const onClick = (): void => {
    setIsOpen(prev => !prev)
  }
  
  const onClose = (): void => {
    setIsOpen(false)
  }
  
  // ========================================== //
  
  return (
    <div>
      <button onClick={onClick}>
        Toggle popover relatively body
      </button>
      <Popover
        isOpen={isOpen}
        onClose={onClose}
        options={OPTIONS}
      >
        <div className={CLASS_NAME}>
          Popper Content Centered
        </div>
      </Popover>
    </div>
  )
}
